import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { useState } from "react";
import { toast } from "react-toastify";
import { useHistory } from "react-router-dom";
const Edit = () => {
  const { id } = useParams();
  const contacts = useSelector((state) => state);
  const dispatch = useDispatch();
  const history = useHistory();
  const currentContact = contacts.find(function (contact) {
    return contact.id === parseInt(id);
  });
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");

  useEffect(() => {
    if (currentContact) {
      setName(currentContact.name);
      setEmail(currentContact.email);
      setPhone(currentContact.phone);
    }
  }, [currentContact]);
  const getNameInput = (event) => {
    setName(event.target.value);
  };
  const getPhoneInput = (event) => {
    setPhone(event.target.value);
  };
  const getEmailInput = (event) => {
    setEmail(event.target.value);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    const checkEmail = contacts.find(function (contact) {
      return contact.id !== parseInt(id) && contact.email === email;
    });
    const checkPhone = contacts.find(function (contact) {
      return contact.id !== parseInt(id) && contact.phone === phone && phone;
    });

    if (!email || !phone || !name) {
      return toast.warning("Please fill all the fields");
    }
    if (checkEmail) {
      return toast.error("This email is already exists");
    }
    if (checkPhone) {
      return toast.error("This phone no is already exists");
    }
    const data = {
      id: parseInt(id),
      name,
      phone,
      email,
    };
    dispatch({ type: "UPDATE_CONTACT", payload: data });
    toast.success("Contact updated successfully!");
    history.push("/contact-app");
  };
  return (
    <div className="container">
      {currentContact ? (
        <>
          <div className="row">
            <h1 className="display-3 text-center">Edit Contact {id}</h1>
            <div className="col-md-6 shadow mx-auto text-center my-4">
              <form onSubmit={handleSubmit}>
                <div className="form-group py-3">
                  <input
                    type="text"
                    placeholder="Enter Name"
                    className="form-control"
                    onChange={getNameInput}
                    value={name}
                  ></input>
                </div>
                <div className="form-group py-3">
                  <input
                    type="text"
                    placeholder="Enter Phone No"
                    className="form-control"
                    onChange={getPhoneInput}
                    value={phone}
                  ></input>
                </div>
                <div className="form-group py-3">
                  <input
                    type="email"
                    placeholder="Enter email id"
                    className="form-control"
                    onChange={getEmailInput}
                    value={email}
                  ></input>
                </div>
                <div className="form-group py-2 ">
                  <button type="submit" className="btn btn-dark mx-2">
                    Update
                  </button>
                  <Link to="/contact-app" className="btn btn-danger">
                    Cancel
                  </Link>
                </div>
              </form>
            </div>
          </div>
        </>
      ) : (
        <h1>This contact id: {id} does not exist</h1>
      )}
    </div>
  );
};
export default Edit;
