import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
const Add = () => {
  const contacts = useSelector((state) => state);
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const dispatch = useDispatch();
  const history = useHistory();
  const getNameInput = (event) => {
    setName(event.target.value);
  };
  const getPhoneInput = (event) => {
    setPhone(event.target.value);
  };
  const getEmailInput = (event) => {
    setEmail(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const checkEmail = contacts.find(function (contact) {
      return contact.email === email && email;
    });
    const checkPhone = contacts.find(function (contact) {
      return contact.phone === phone && phone;
    });

    if (!email || !phone || !name) {
      return toast.warning("Please fill all the fields");
    }
    if (checkEmail) {
      return toast.error("This email already exists");
    }
    if (checkPhone) {
      return toast.error("This phone no. already exists");
    }
    const data = {
      id: contacts[contacts.length - 1].id + 1,
      name,
      phone,
      email,
    };

    dispatch({ type: "ADD_CONTACT", payload: data });
    toast.success("Contact added successfully!");
    history.push("/contact-app");
  };

  return (
    <div className="container">
      <div className="row">
        <h1 className="display-3 text-center">Add Contact</h1>
        <div className="col-md-6 shadow mx-auto text-center my-4">
          <form onSubmit={handleSubmit}>
            <div className="form-group py-3">
              <input
                type="text"
                placeholder="Enter Name"
                className="form-control"
                value={name}
                onChange={getNameInput}
              ></input>
            </div>
            <div className="form-group py-3">
              <input
                type="text"
                placeholder="Enter Phone No"
                className="form-control"
                value={phone}
                onChange={getPhoneInput}
              ></input>
            </div>
            <div className="form-group py-3">
              <input
                type="email"
                placeholder="Enter email id"
                className="form-control"
                value={email}
                onChange={getEmailInput}
              ></input>
            </div>
            <div className="form-group py-2">
              <button type="submit" className="btn btn-dark form-control">
                Add Contact
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};
export default Add;
