const initialState = [
  {
    id: 0,
    name: "Sayantan Chakravarty",
    phone: "123456789",
    email: "sayantan@gmail.com",
  },
  {
    id: 1,
    name: "Test Name",
    phone: 987654321,
    email: "test@gmail.com",
  },
];
const ContactReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_CONTACT":
      state = [...state, action.payload];
      return state;
    case "UPDATE_CONTACT":
      const updatedState = state.map((contact) => {
        if (contact.id === action.payload.id) {
          return action.payload;
        } else {
          return contact;
        }
      });
      state = updatedState;
      return state;
    case "DELETE_CONTACT":
      const fiteredContact = state.filter((contact) => {
        return contact.id !== action.payload;
      });
      state = fiteredContact;
      return state;
    default:
      return state;
  }
};
export default ContactReducer;
