import React from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
const Home = () => {
  const contacts = useSelector((state) => state);
  const dispatch = useDispatch();
  const deleteContact = (id) => {
    dispatch({ type: "DELETE_CONTACT", payload: id });
    toast.success("Contact deleted successfully");
  };
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-12 text-right my-5">
          <Link to="/contact-app/add" className="btn btn-primary add-contact">
            Add Contact
          </Link>
        </div>

        <div className="col-md-6 mx-auto">
          <h1>React Redux contact book </h1>
          <table className="table">
            <thead className="table table-hover">
              <tr>
                <th scope="col">#</th>
                <th scope="col">Name</th>
                <th scope="col">Phone No</th>
                <th scope="col">Email</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              {contacts.map((contact, id) => {
                return (
                  <tr key={contact.id}>
                    <td>{id + 1}</td>
                    <td>{contact.name}</td>
                    <td>{contact.phone}</td>
                    <td>{contact.email}</td>
                    <td>
                      <div className="action-btn">
                        <Link
                          to={`/contact-app/edit/${contact.id}`}
                          className="btn  btn-small btn-warning mx-2"
                        >
                          Edit
                        </Link>
                        <button
                          type="button"
                          className="btn btn-small btn-danger"
                          onClick={() => {
                            deleteContact(contact.id);
                          }}
                        >
                          Delete
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};
export default Home;
